import React from 'react'
import { Link } from 'react-router'
import auth from '../utils/auth'

const App = React.createClass({

  getInitialState() {
    return {
      loggedIn: auth.loggedIn()
    }
  },

  updateAuth(loggedIn) {
    this.setState({
      loggedIn: !!loggedIn
    })
  },

  componentWillMount() {
    auth.onChange = this.updateAuth
    auth.login()
  },

  render() {
    let Wrapper = (<div>logget out</div>)
    if (this.state.loggedIn) {
        Wrapper = (<div>menggila</div>)
    }
    return (
      <div>
        {this.props.children}
        {Wrapper}
      </div>
    )
  }

})

export default App



